<template>
  <content-section>
    <v-container fill-height>
      <v-row justify="center" align="center">
        <v-col cols="12" sm="8">
          <v-layout wrap class="text-xs-center">
            <template v-for="(item, i) in items">
              <v-flex :key="i" md3 sm4 xs6>
                <v-progress-circular
                  @click="setText(item)"
                  rotate="-90"
                  size="150"
                  width="10"
                  :value="item.value"
                  color="blue-grey lighten-1"
                  class="ma-2"
                >
                  {{ item.text }}
                </v-progress-circular>
              </v-flex>
            </template>
          </v-layout>
        </v-col>
      </v-row>
    </v-container>
  </content-section>
</template>

<script>
export default {
  name: "LanguagesView",

  data: () => ({
    items: [
      {
        text: "English",
        value: 85,
      },
      {
        text: "Cantonese",
        value: 100,
      },
      {
        text: "Mandarin",
        value: 95,
      },
      {
        text: "Japanese",
        value: 15,
      },
    ],
  }),

  methods: {
    setText(item) {
      if (item.value >= 90) {
        item.text = "Fluent";
      } else if (item.value < 90 && item.value >= 50) {
        item.text = "Professional";
      } else if (item.value < 50) {
        item.text = "Beginner";
      }
    },
  },

  mounted() {
    document.title = "Language Proficiency";
  },
};
</script>
